import React from 'react'
import Table from "../../components/Table";
import Button from '@mui/material/Button';
import DateRangePicker from '@mui/lab/DateRangePicker';
import AdapterJalali from '@date-io/date-fns-jalali';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import Loding from '../../components/Loding'
const axios = require('axios').default;

function Home({change}) {
    const [state, setState] = React.useState(null)
    const [value, setValue] = React.useState([null, null]);
    const [deletet, setDeletet] = React.useState(null)
    React.useEffect(() => {
        setTimeout(() => {
            filterTime()
        }, 100);      
    }, [deletet])

    const filterTime = () => {
            axios.get('https://hoteldari.herokuapp.com/person')
                .then((response) => {
                    let filterState = response.data.filter((item) => {
                        return (
                            new Date(item.curentDate).getTime() >= new Date(value[0]).getTime()
                            &&
                            new Date(item.curentDate).getTime() <= new Date(value[1]).getTime()
                        )
                    }
                    )
                    if (value[0] !== null) {
                        setState(filterState)
                    } else { setState(response.data) }
                })
                .catch((err) => alert(err))
    }
    const handleFilter = () => {
        filterTime()
    }
    function separate(Number) {
        Number += '';
        Number = Number.replace(',', '');
        let x = Number.split('.');
        let y = x[0];
        let z = x.length > 1 ? '.' + x[1] : '';
        var rgx = /(\d+)(\d{3})/;
        while (rgx.test(y))
            y = y.replace(rgx, '$1' + ',' + '$2');
        return y + z;
    }
    const sum = state === null ? 0 : state.reduce((total, number) => total + parseInt(number.totalPrice), 0);
    return (
        <>
            {
                state === null ?
                    <Loding />
                    :
                    <div style={{ marginTop: 40 }}>
                        <div className='d-flex  m-2'>
                            <div className='ms-4 my-2'>
                                <h3>کل درآمد: {separate(sum)} تومان </h3>
                            </div>
                            <LocalizationProvider dateAdapter={AdapterJalali}>
                                <DateRangePicker
                                    startText="از تاریخ"
                                    endText="این تاریخ"
                                    value={value}
                                    onChange={(newValue) => {
                                        setValue(newValue);
                                    }}
                                    renderInput={(startProps, endProps) => (
                                        <React.Fragment>
                                            <TextField id="fildInpH" {...startProps} />
                                            <Box sx={{ mx: 2 }}> تا </Box>
                                            <TextField id='fildExpH'  {...endProps} />
                                        </React.Fragment>
                                    )}
                                />
                            </LocalizationProvider>
                            <Button
                                variant="contained"
                                color='warning'
                                className='m-2'
                                onClick={handleFilter}

                            >گزارش</Button>
                        </div>
                        <Table  state={state} setDeletet={setDeletet} />
                    </div>
            }
        </>
    );
}

export default Home;
