import * as React from 'react';
import {
    Routes,
    Route
} from "react-router-dom";
import PropTypes from 'prop-types';
import AppBar from '@mui/material/AppBar';
import Button from '@mui/material/Button';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import CssBaseline from '@mui/material/CssBaseline';
import useScrollTrigger from '@mui/material/useScrollTrigger';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import img from '../static/img/logo.png'
import Home from '../pages/Login/Home';
import Setting from '../pages/Login/Setting';
import Form from '../pages/Login/Form';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import moment from 'jalali-moment'


function ElevationScroll(props) {

    const { children, window } = props;
    // Note that you normally won't need to set the window ref as useScrollTrigger
    // will default to window.
    // This is only being set here because the demo is in an iframe.
    const trigger = useScrollTrigger({
        disableHysteresis: true,
        threshold: 0,
        target: window ? window() : undefined,
    });

    return React.cloneElement(children, {
        elevation: trigger ? 4 : 0,
    });
}

ElevationScroll.propTypes = {
    children: PropTypes.element.isRequired,
    /**
     * Injected by the documentation to work in an iframe.
     * You won't need it on your project.
     */
    window: PropTypes.func,
};

export default function ElevateAppBar(props) {
    const [open, setOpen] = React.useState(false);
    const [change, setChange] = React.useState("");


    const handleClickOpen = (e) => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };
    React.useEffect(()=> {
        document.addEventListener('keydown',(event)=>{
            if(event.keyCode === 27){
                handleClickOpen();
            }
        }, false)
    },[])
    function handLogout()  {
        props.setIslog(false)
        localStorage.setItem("login", null);
    }
    return (
        <React.Fragment>
            <CssBaseline />
            <ElevationScroll {...props}>
                <AppBar>
                    <Toolbar sx={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
                        <Typography sx={{ display: "flex", padding: 1, alignItems: "center" }} variant="h6" component="div">
                            <img style={{ marginLeft: 20 }} src={img} alt="" />
                            <strong>نرم افزار هتل داری دانشگاه شمال</strong>
                        </Typography>
                        <div>
                            <span style={{fontSize:14 , marginLeft:20}}>{moment().locale('fa').format('YYYY/MM/DD')} : تاریخ امروز </span>
                            <Button
                                color='error'
                                variant="contained"
                                style={{ marginBlock: 20 }}
                                type="submit"
                                onClick={handleClickOpen}
                            >خارج شدن</Button>
                        </div>
                    </Toolbar>
                </AppBar>
            </ElevationScroll>
            <Toolbar />
            <Container>
                <Routes>
                    <Route path="/"  element={<Home change={change} />} />
                    <Route path="Form" setChange={setChange} element={<Form setChange={setChange} />} />
                    <Route path="setting" element={<Setting />} />
                </Routes>
            </Container>
            <Dialog
                open={open}
                onClose={handleClose}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogTitle id="alert-dialog-title">
                    {"آیا میخواهید از نرم افزار خارج شوید؟"}
                </DialogTitle>
                <DialogActions>
                    <Button
                        variant="contained"
                        onClick={handleClose}
                    >لغو</Button>
                    <Button
                        color='error'
                        variant="contained"
                        onClick={handLogout}
                        autoFocus>
                        تایید
                    </Button>
                </DialogActions>
            </Dialog>
        </React.Fragment>
    );
}
