function Print({user}) {
    function separate(Number) {
        Number += '';
        Number = Number.replace(',', '');
        let x = Number.split('.');
        let y = x[0];
        let z = x.length > 1 ? '.' + x[1] : '';
        var rgx = /(\d+)(\d{3})/;
        while (rgx.test(y))
          y = y.replace(rgx, '$1' + ',' + '$2');
        return y + z;
      }
    return (
        <>
        <div id='print' className="printStyle ">
            <div className=" border p-2 rounded-3 bg-light">
                <div className="d-flex bg-white">
                    <div className="col-5 border p-2">شماره اتاق: {user[0].number}</div>
                    <div className="col-7 border p-2">نام: {user[0].name}</div>
                </div>
                <div className="d-flex bg-white">
                    <div className="col border p-2">تلفن: {user[0].phone}</div>
                    <div className="col border p-2">شهر : {user[0].address}</div>
                </div>
                <div className="d-flex bg-white">
                    <div className="col border p-2">تاریخ ورود : {user[0].dateArrival}</div>
                    <div className="col border p-2">تاریخ خروج: {user[0].dateExport}</div>
                </div>
                <div className="d-flex bg-white">
                    <div className="col border p-2" style={{height:100}}>توضیحات:</div>
                </div>
                <div className="d-flex bg-white">
                    <div className="col border p-2">جمع کل : {separate(user[0].totalPrice)} تومان</div>
                </div>
                <div className="d-flex bg-white">
                    <div className="col border p-2 py-3" style={{lineHeight:2}}>وجه فوق طی فیش یا چک شماره ......................به تاریخ ................................. به حساب جاری ............................................ واریز گردید.</div>
                </div>
                <div className="d-flex bg-white border">
                    <div className="col  p-3 pt-3 pb-5" style={{fontSize:16}}>امضاء مشتری</div>
                    <div className="col  p-3 pt-3 pb-5" style={{fontSize:16}}>امضاء مسئول مهمانسرا</div>
                </div>
            </div>
        </div>
        {/* PRINT ENDS HERE */}
        </>
    );
    
}

export default Print;