import * as React from 'react';
import IconButton from '@mui/material/IconButton';
import Input from '@mui/material/Input';
import InputAdornment from '@mui/material/InputAdornment';
import FormControl from '@mui/material/FormControl';
import Visibility from '@mui/icons-material/Visibility';
import VisibilityOff from '@mui/icons-material/VisibilityOff';
import InputLabel from '@mui/material/InputLabel';
import Button from '@mui/material/Button';
import im from '../static/img/logo.png'

const axios = require('axios').default;

function Login({ setIslog }) {
    const [log, setLog] = React.useState('8b673/.xz0ia-9duyahd@328y');
    React.useEffect(() => {
        axios.get('https://hoteldari.herokuapp.com/person/auth')
        .then(({data})=>{
            setLog(data[0].pass)
        })
        .catch((err)=> {
            alert(err)
            return (
                <h1>404</h1>
            )
        })    
    }, [])
    const [values, setValues] = React.useState({
        amount: '',
        password: '',
        weight: '',
        weightRange: '',
        showPassword: false,
    });
    const handleChange = (prop) => (event) => {
        setValues({ ...values, [prop]: event.target.value });
    };

    const handleClickShowPassword = () => {
        setValues({
            ...values,
            showPassword: !values.showPassword,
        });
    };

    const handleMouseDownPassword = (event) => {
        event.preventDefault();
    };

    return (
        <form className='login' onSubmit={(e) => {
            e.preventDefault();
            if (e.target.elements[0].value === log) {
                setIslog(true)
                localStorage.setItem("login", "admin");
            } else {
                localStorage.setItem("login", null);
                alert("پسورد اشتباه است")
            }
        }}>
            <img src={im} alt="logo" />
            <h2 style={{ marginBlock: 10, color: "#323232" }}>ورود به نرم افزار هتل داری</h2>
            <FormControl sx={{ m: 1, width: '25ch' }} variant="standard">
                <InputLabel htmlFor="standard-adornment-password">Password</InputLabel>
                <Input
                    id="standard-adornment-password"
                    type={values.showPassword ? 'text' : 'password'}
                    value={values.password}
                    onChange={handleChange('password')}
                    endAdornment={
                        <InputAdornment position="end">
                            <IconButton
                                aria-label="toggle password visibility"
                                onClick={handleClickShowPassword}
                                onMouseDown={handleMouseDownPassword}
                            >
                                {values.showPassword ? <VisibilityOff /> : <Visibility />}
                            </IconButton>
                        </InputAdornment>
                    }
                />
                <Button
                    variant="contained"
                    style={{ marginBlock: 20 }}
                    type="submit"
                >وارد شدن</Button>
            </FormControl>
        </form >
    );
}

export default Login;