/* eslint-disable react/jsx-no-duplicate-props */
import * as React from 'react';
import TextField from '@mui/material/TextField';
import { useNavigate } from "react-router-dom";
import DateRangePicker from '@mui/lab/DateRangePicker';
import AdapterJalali from '@date-io/date-fns-jalali';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import Box from '@mui/material/Box';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import Slider from '@mui/material/Slider';
import Checkbox from '@mui/material/Checkbox';
import FormControlLabel from '@mui/material/FormControlLabel';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import Button from '@mui/material/Button';
const axios = require('axios').default;

function Form({setChange}) {
    const redirect = useNavigate()
    
    const [typeRoom, setTypeRoom] = React.useState('');
    const [post1, setpost1] = React.useState({});
    const [total, setTotal] = React.useState(0);
    const [off, setOff] = React.useState(false);
    const [isSubmit, setIsSubmit] = React.useState(false);
    const [open, setOpen] = React.useState(false);
    const [Data, setData] = React.useState(false);
    const [value22, setValue22] = React.useState([null, null]);

    const ref1 = React.useRef(null)
    const ref2 = React.useRef(null)
    const ref3 = React.useRef(null)
    const ref4 = React.useRef(null)
    const ref5 = React.useRef(null)
    const ref55 = React.useRef(null)



    function handleChange(event) {
        setTypeRoom(event.target.value);
    }

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    ///handeler data 
    function handleData() {
        let numberRoom = ref1.current.children[1].children[0].value
        let namE = ref2.current.children[1].children[0].value
        let address = ref3.current.children[1].children[0].value
        let phone = ref55.current.children[1].children[0].value
        if (post1 !== null) {
            axios.post('https://hoteldari.herokuapp.com/person', {
                "number": numberRoom,
                "name": namE,
                "address": address,
                "phone": phone,
                "dateArrival": post1.dateArrival,
                "dateExport": post1.dateExport,
                "typeRoom": post1.typeRoom,
                "discount": off ? parseInt(post1.ofF) : 0,
                "lengthDay": post1.length,
                "totalPrice": post1.totalPrice,
                "curentDate": new Date()
            })
                .catch(function (error) {
                    alert(error);
                });
        }
        handleClose();
        setChange(new Date())
        redirect('/')
    }

    function handelSubmit(e) {
        e.preventDefault()
        let typeRoom = ref4.current.children[1].value
        let ofF = e.target.elements[15].value
        let dayInput = document.getElementById('fildInp').value.substring(8, 10)
        let monthInput = document.getElementById('fildInp').value.substring(5, 7)
        let yearInput = document.getElementById('fildInp').value.substring(2, 4)
        let dayexport = document.getElementById('fildExp').value.substring(8, 10)
        let monthexport = document.getElementById('fildExp').value.substring(5, 7)
        let yeaExport = document.getElementById('fildExp').value.substring(2, 4)
        /// number day
        function lengthDay() {
            return (
                (parseInt(dayexport) + (parseInt(monthexport) * 30) + (yeaExport * 365)) - (parseInt(dayInput) + (parseInt(monthInput) * 30) + (yearInput * 365)) 
                )
                
            }
        function TotalPrice() {
            return (
                (lengthDay() * typeRoom) * (off ? ((100 - ofF) / 100) : 1)
            )

        }
        function separate(Number) {
            Number += '';
            Number = Number.replace(',', '');
            let x = Number.split('.');
            let y = x[0];
            let z = x.length > 1 ? '.' + x[1] : '';
            var rgx = /(\d+)(\d{3})/;
            while (rgx.test(y))
                y = y.replace(rgx, '$1' + ',' + '$2');
            return y + z;
        }

        setTotal(separate(TotalPrice()));
        setpost1({
            dateArrival: document.getElementById('fildInp').value,
            dateExport: document.getElementById('fildExp').value,
            typeRoom: typeRoom + "تومان",
            discount: off ? parseInt(ofF) : 0,
            length: lengthDay(),
            totalPrice: TotalPrice(),
            ofF: ofF
        })


    }

    function onSubmitForPost() {
        let numberRoom = ref1.current.children[1].children[0].value
        let namE = ref2.current.children[1].children[0].value
        let phone = ref55.current.children[1].children[0].value

        if (numberRoom.trim() === "" || namE.trim() === "" || phone.trim() === "") {
            alert('فیلد ها باید پر باشند')
            return false
        }
        handleClickOpen();
    }

    return (
        <div style={{ marginTop: 40, width: "75%", maxWidth:1440, margin: "auto" }}>
            <form onSubmit={handelSubmit}>
                <h1 style={{ textAlign: "center", margin: 30, color: "#525252" }}>مهمانسرا دانشگاه شمال</h1>
                <div style={{ display: "flex" }}>
                    <TextField className='w-25' error={false} ref={ref1} id="outlined-basic" sx={{ marginRight: "10px" }} label="شماره اتاق" variant="outlined" />
                    <TextField className='w-100' ref={ref2} id="outlined-basic"  fullWidth label="نام و نام خانوادگی" variant="outlined" />
                </div>
                <div style={{ display: "flex", marginTop: 10 }}>
                <TextField ref={ref55} id="outlined-basic" sx={{ marginRight: "10px" }} fullWidth label="شماره تلفن" variant="outlined" />

                    <TextField ref={ref3} id="outlined-basic"   fullWidth label="شهر(اختیاری)" variant="outlined" />
                </div>
                <div style={{ display: "flex", marginTop: 10, justifyContent: "space-between" }}>
                    <LocalizationProvider dateAdapter={AdapterJalali}>
                        <DateRangePicker
                            startText="تاریخ ورود"
                            endText="تاریخ خروج"
                            value={value22}
                            onChange={(newValue) => {
                                setValue22(newValue);
                            }}
                            renderInput={(startProps, endProps) => (
                                <React.Fragment>
                                    <TextField id="fildInp" {...startProps} />
                                    <Box sx={{ mx: 2 }}> تا </Box>
                                    <TextField id='fildExp' ref={ref5} {...endProps} />
                                </React.Fragment>
                            )}
                        />
                    </LocalizationProvider>
                    <FormControl className='w-75 me-5'>
                        <InputLabel id="demo-simple-select-label">نوع اتاق</InputLabel>
                        <Select
                            labelId="demo-simple-select-label"
                            id="demo-simple-select"
                            value={typeRoom}
                            label="نوع اتاق"
                            onChange={handleChange}
                            ref={ref4}
                        >
                            <MenuItem value={250000}>اتاق یک نفره</MenuItem>
                            <MenuItem value={300000}>اتاق دو نفره</MenuItem>
                            <MenuItem value={500000}>سوئیت پنج نفره</MenuItem>
                        </Select>
                    </FormControl>
                </div>
                <div style={{ display: "flex", marginTop: 10, justifyContent: "space-between", alignItems: "center" }}>

                    <div className='range-off'>
                        <div className="top-salsd">
                            <label htmlFor="off">
                                درصد تخفیف
                            </label>
                            <FormControlLabel onClick={() => setOff(!off)} control={<Checkbox defaultChecked={off} />} label="تخفیف دارد؟" />
                        </div>
                        <Slider
                            size="small"
                            defaultValue={70}
                            aria-label="Small"
                            valueLabelDisplay="auto"
                            dir="ltr"
                            sx={{ m: 1 }}
                            disabled={!off}
                        />

                    </div>
                </div>
                <Button variant="contained" type="submit" fullWidth>محاسبه</Button>
            </form>
            <Button variant="outlined" style={{ cursor: "revert" }} fullWidth >{total + " تومان"}</Button>
            <Button
                variant="contained"
                type='submit'
                color="success"
                onClick={onSubmitForPost}
                disabled={parseInt(total) > 0 ? false : true || !isSubmit}
                fullWidth
            >ثبت اطلاعات</Button>
            <Dialog
                open={open}
                onClose={handleClose}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogTitle id="alert-dialog-title">
                    {"آیا اطلاعاتی که ثبت شده صحیح است؟"}
                </DialogTitle>
                <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                        در غیر این صورت لغو بزنید اصلاح کنید تا اطلاعات درست در دیتابیس ذخیره بشه
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClose}>لغو</Button>
                    <Button onClick={handleData} autoFocus>
                        تایید
                    </Button>
                </DialogActions>
            </Dialog>
        </div>
    );
}

export default Form;
