import * as React from 'react';
import { NavLink } from "react-router-dom";
import Button from '@mui/material/Button';
import BottomNavigation from '@mui/material/BottomNavigation';
import BottomNavigationAction from '@mui/material/BottomNavigationAction';
import SettingsApplicationsRoundedIcon from '@mui/icons-material/SettingsApplicationsRounded';
import HomeRoundedIcon from '@mui/icons-material/HomeRounded';
import FormatAlignCenterRoundedIcon from '@mui/icons-material/FormatAlignCenterRounded';

export default function SimpleBottomNavigation() {

    return (
        <BottomNavigation className='navb'>
            <NavLink to='/setting' style={{textDecoration:"none"}}>
                <Button sx={{display:'flex', flexDirection:"column" , height:"100%"}}>
                    <SettingsApplicationsRoundedIcon />
                    <span>تنظیمات</span>
                </Button>
            </NavLink>
            <NavLink to='/' style={{textDecoration:"none"}}>
                <Button sx={{display:'flex', flexDirection:"column" , height:"100%" , marginTop:-4 }}>
                    <HomeRoundedIcon />
                    <span>داشبورد</span>
                </Button>
            </NavLink>
            <NavLink to='/Form' style={{textDecoration:"none"}}>
                <Button sx={{display:'flex', flexDirection:"column" , height:"100%"}}>
                    <FormatAlignCenterRoundedIcon />
                    <span>ثبت فرم</span>
                </Button>
            </NavLink>
        </BottomNavigation>
    );
}